body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  overflow-y: scroll;
}

header.MuiAppBar-colorPrimary {
  background: red;
}

div.MuiToolbar-root {
  display: flex;
  justify-content: space-between;
  padding-right: 16px;
}

div.MuiToolbar-root .MuiTypography-root {
  display: inline;
}

div.MuiToolbar-root .MuiTypography-root * {
  vertical-align: text-top;
}

button.MuiButtonBase-root {
  margin-right: 0;
}

div.MuiDrawer-paperAnchorLeft {
  left: auto;
  right: 0;
}

div.MuiDrawer-paperAnchorDockedLeft {
  border-left: 1px solid rgba(0, 0, 0, 0.12);
  border-right: none;
}

div.MuiListItemIcon-root {
  min-width: 30px;
}

.input__container {
  display: flex;
  flex-direction: column;
  padding-top: 1.2rem;
}

.input__container input[type=text] {
  display: inline-flex;
  width: 72%;
  font-size: 1.7rem;
  border-radius: 4px;
  border: 0.1rem inset;
  padding: 0.3rem;
}

.input__container select {
  display: inline-flex;
  width: 72%;
  font-size: 1.7rem;
  border-radius: 4px;
  border: 0.1rem inset;
  padding: 0.3rem;
}

.input__container .select__container {
  display: flex;
  margin-top: 0.4rem;
}

.input__container .MuiInputLabel-shrink {
  transform: none;
  transform-origin: initial;
}

.input__container .MuiInputLabel-formControl {
  position: relative;
}

.input__container .MuiInputLabel-root {
  font-size: 1rem;
  text-align: left;
  color: black;
}

.input__container .MuiInputAdornment-root,
.input__container .MuiInputAdornment-root p {
  color: black;
}

.MuiTypography-root .info .MuiAlert-icon,
.MuiTypography-root .info .MuiAlert-action,
.input__container .MuiAlert-icon,
.input__container .MuiAlert-action {
  display: none;
}

.input__container label + .MuiInput-formControl {
  margin-top: 0.4rem;
}

.input__container .MuiInput-underline::before,
.input__container .MuiInput-underline::after,
.input__container .MuiInput-underline:hover:not(.Mui-disabled):before {
  border: 0;
}

.text-vertical-center * {
  vertical-align: text-top;
}

.input__container .btn-info {
  min-width: 0;
  padding: 0;
  margin: 0 6px;
}

.input__container .MuiAlert-message p:last-child {
  padding-bottom: 0;
  margin-block-end: 0;
}

.input__group .insulinunits__button__addproportionatecarbs,
.screen__container .proportionatecarbs__button__addtoinsulin {
  padding-top: 1.3rem;
  display: flex;
  justify-content: flex-end;
}

.screen__container .proportionatecarbs__button__addtoinsulin {
  padding-top: 2rem;
}

.input__group .insulinunits__button__addproportionatecarbs .MuiButtonBase-root {
  width: initial !important;
}

.input__group .MuiAccordion-root,
.input__group .MuiAccordion-root.Mui-expanded {
  margin-top: 2rem;
  box-shadow: none;
}

.input__group .MuiAccordionSummary-root,
.input__group .MuiAccordionSummary-root.Mui-expanded {
  background: lightgrey;
  margin: 0;
  min-height: 0;
  padding: 6px 8px;
  border-radius: 4px;
}

.input__group .MuiAccordionSummary-content,
.input__group .MuiAccordionSummary-content.Mui-expanded {
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  margin: auto;
  display: flex;
  line-height: 1.75;
  padding: 0;
  justify-content: center;
}

.input__group .MuiIconButton-root {
  padding: 0 10px;
}

.input__group .MuiAccordionDetails-root {
  flex-direction: column;
  padding: 0.5rem 0;
}

.screen__container {
  max-width: 400px;
  margin: auto;
}

.screen__container .button {
  background: lightgrey;
}

.MuiTypography-root.section__divider {
  font-weight: bold;
  font-size: 1.1em;
  text-align: left;
  padding-top: 2em;
  border-bottom: 1px solid lightgrey;
  text-transform: uppercase;
}

.MuiTypography-root.section__divider .info {
  text-transform: none;
  white-space: normal;
}

.insulinunits_input.input__group {
  padding-top: 1em;
}

.proportionatecarbs__input__carbsperweight__container,
.proportionatecarbs__input__amountconsumed__container {
  display: flex;
}

.proportionatecarbs__input__carbsperweight__container > *:first-child,
.proportionatecarbs__input__amountconsumed__container > *:first-child {
  padding-right: 1em;
}

.proportionatecarbs__input__carbsperweight__container > *:last-child,
.proportionatecarbs__input__amountconsumed__container > *:last-child {
  padding-left: 1em;
}
